.page {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: normal;
}
.page__body {
  display: grid;
  grid-template-rows: min-content 1fr min-content min-content;
  margin: 0;
}
.page-header {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 500;
  margin-top: 12px;
}
@media (min-width: 768px) {
  .page-header {
    border: none;
    margin-top: 24px;
  }
}
@media (min-width: 1440px) {
  .page-header {
    margin-top: 53px;
  }
}
.page-header__wrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  position: relative;
  z-index: 2;
  padding: 0;
}
@media (min-width: 768px) {
  .page-header__wrapper {
    align-items: center;
    padding: 0 30px;
  }
}
@media (min-width: 1440px) {
  .page-header__wrapper {
    padding: 0 110px;
  }
}
.page-header__site-nav {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  border-bottom: 1px solid #d9d9d9;
  width: 100%;
  padding: 0 20px 10px 20px;
}
@media (min-width: 768px) {
  .page-header__site-nav {
    border: none;
    padding: 0;
    width: auto;
  }
}
.page-header__link {
  display: block;
}
.page-header__link--logo-text {
  display: flex;
  justify-content: center;
  align-items: center;
}
.page-header__button {
  display: flex;
  justify-content: center;
  align-items: center;
}
.page-header__toogle {
  position: relative;
  cursor: pointer;
  background-color: transparent;
  outline: none;
  border: none;
  padding: 0;
  height: 18px;
  width: 24px;
}
@media (min-width: 768px) {
  .page-header__toogle {
    display: none;
  }
}
.page-header__toogle--open::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 24px;
  height: 1.5px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}
.page-header__toogle--closed::before,
.page-header__toogle--closed::after {
  content: "";
  position: absolute;
  left: 50%;
  width: 23px;
  height: 1.5px;
  background-color: #000000;
}
.page-header__toogle--closed .page-header__toogle--nojs {
  display: none;
}
.page-header__toogle--closed::before {
  transform: translateX(-50%) rotate(45deg);
  box-shadow: none;
}
.page-header__toogle--closed::after {
  transform: translateX(-50%) rotate(-45deg);
}
.logo:hover {
