.promo {
  color: #FFFFFF;
  position: relative;
  padding-top: 11px;
  z-index: 0;
}
.promo::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 302px;
  top: 0;
  right: 0;
  background-image: url("../img/promo-bg-image-mobile.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  z-index: -3;
}
@media (min-resolution: 2dppx) {
  .promo::before {
    background-image: url("../img/promo-bg-image-mobile@2x.jpg");
  }
}
.promo::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 302px;
  top: 0;
  right: 0;
  background-color: rgba(104, 183, 56, 0.85);
  z-index: -2;
}
@media (min-width: 768px) {
  .promo {
    background-image: none;
    color: #000000;
    padding-top: 43px;
  }
  .promo::before {
    display: none;
  }
  .promo::after {
    display: none;
  }
}
@media (min-width: 1440px) {
  .promo {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-top: 105px;
    width: 100%;
  }
  .promo::before {
    display: block;
    background-image: url("../img/promo-bg-image-desktop.jpg");
    background-size: cover;
    background-position: right top;
    height: 694px;
    width: 50%;
    top: -117px;
    right: 0;
  }
  .promo::after {
    display: block;
    height: 694px;
    width: 50%;
    top: -117px;
  }
}
@media (min-width: 1440px) and (min-resolution: 2dppx) {
  .promo::before {
    background-image: url("../img/promo-bg-image-desktop@2x.jpg");
  }
}
